<template>
  <div>
    <section class="head searchHead clearfix">
      <div class="backWrap">
        <a class="icon icon_manue"> manue </a>
      </div>
      <div class="searchBtn" @click="$router.push('/cart')">
        <a class="iconfont back">&#xe669;</a>
      </div>
      <div class="search" @click="$router.push('/search')">
        <van-field
          v-model="searchValue"
          clearable
          left-icon="mysearch"
          placeholder="Looking for sth? Search here."
        />
      </div>
    </section>
    <section class="downListWrap">
      <div class="commonPro">
        <ul class="clearfix turnLine">
          <li v-for="(item, index) in productList" :key="index" >
            <div class="tips_cutOff" v-if="item.percentage!='0%'">
              {{ item.percentage }} OFF
            </div>
            <div class="tips_sale"></div>
            <div class="imgWrap">
              <img class="img" :src="item.spuMainImage" />
            </div>
            <div class="rightMain">
              <div class="name">{{ item.name }}</div>
              <div class="downWrap">
                <div class="bottomPrice">
                  <p class="red">{{ item.priceRange }} AED</p>
                  <p class="gray">{{ item.priceRangeLine }} AED</p>
                  <p v-if="item.isTypeFour == 1">Free Shipping</p>
                </div>
                <div class="downShare">
                  <p class="others">{{ item.sumSku }} Other Items</p>
                  <div
                    :class="{ active: item.share }"
                    @click.stop="shareShow(item)"
                  >
                    <span class="share iconfont"> &#xe650; </span>
                  </div>
                  <div
                    class="tips_love iconfont"
                    :class="{ active: wishCode.indexOf(item.spuCode) > -1 }"
                    @click.stop="handlerLove(item)"
                  >
                    {{
                      wishCode.indexOf(item.spuCode) > -1
                        ? "&#xe653;"
                        : "&#xe652;"
                    }}
                  </div>
                  <div
                    :class="{ active: item.cart }"
                    @click.stop="addChart(item)"
                  >
                    <span class="cart iconfont"> &#xe669; </span>
                  </div>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </section>

    <!-- 添加购物车 -->
    <add-cart ref="addCart"> </add-cart>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { getProListBySpuList } from "@/api/product";
export default {
  name: "shareList",
  data() {
    return {
      spuCodes: this.$route.query.spuCodes,
      productList: [],
      searchValue:""
    };
  },
  computed: {
    ...mapGetters(["indexData", "categoryOne", "wishCode", "wishes"])
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      getProListBySpuList({
        spuCodeList: this.spuCodes
      }).then(res => {
        this.productList = res.data.result;
      });
    },
    getStr(obj) {
      var index = obj.indexOf("-");
      if (index != -1) {
        var str = obj.substring(0, index - 1);
        return str;
      } else {
        return obj;
      }
    },
     shareShow(item) {
    this.$store
      .dispatch("share", {
        imageUrl: [item.spuMainImage],
        url: "proDetail?spuCode=" + item.spuCode,
        title: item.name
      })
      .then(res => {});
  },
  addChart(data) {
    this.$refs.addCart.load(data);
  },
  handlerLove(item) {
    let index = this.wishCode.indexOf(item.spuCode);
    let isAdd = index == -1;
    this.$store
      .dispatch(
        isAdd ? "addWish" : "deleteWish",
        isAdd
          ? { type: "spu", code: item.spuCode }
          : { id: this.wishes.result[index].id }
      )
      .then(res => {
        if(isAdd){
            this.$toast("Move to Whislist Successfully");
          }else{
            this.$toast("Remove From Whislist Successfully");
          }
        this.$store.dispatch("myWishes");
      });
  }

  },
 
};
</script>

<style lang="scss" scoped>
@import "../common/css/search.scss";
ul {
  border-top: 1px solid #f6f6f6;
}
.commonPro .downShare {
  bottom: 0;
}
.searchHead .search {
  margin: 0 33px 0 30px;
}
.searchHead .searchBtn {
  width: 20px;
}
</style>



